@import "../../../assets/css/var";

.input_wrap {
  background: #403858;
  border-radius: 10px;
  margin-top: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid transparent;
  &:hover {
    border: 1px solid #C1A0FF;
  }

  &_active {
    border: 1px solid #C1A0FF;
  }
  .with_max_input {
    background: #403858;
    border: none;
    border-radius: 10px;
    font-size: 20px;
    line-height: 24px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: #ffffff;
    padding: 10px;
    width: 100%;

    &:hover,
    &:focus {
      border: none;
    }
    &::placeholder {
      color: $white;
      opacity: 0.3;
    }
  }
  .max_btn {
    background: $max-btn-bg;
    border-radius: 15px;
    padding: 4px 14px;
    cursor: pointer;
    font-size: 12px;
    font-weight: 600;
    line-height: 17px;
    color: $white;
    margin-right: 10px;
    white-space: nowrap;
  }
}