@import '../../assets/css/theme';
.currency {
  &_card {
    background: $color-card-bg;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px;
  }
  &_header {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    font-weight: 600;
    color: #ffffff;
    height: 40px;
    align-items: center;
    margin-bottom: 10px;
    &_icon {
      width: 40px;
      height: 40px;
      margin-left: -10px;
    }
  }
  &_total_balance {
    font-size: 14px;
    font-weight: 600;
    color: rgba(#fff, 0.7);
    line-height: 20px;
    margin-bottom: 8px;
  }
  &_total_value {
    font-size: 30px;
    font-weight: bold;
    color: #ffffff;
    line-height: 36px;
    margin-bottom: 20px;
    font-family: Helvetica-Bold, Helvetica;
  }
  &_block {
    background: $color-info-bg;
    border-radius: 10px;
    padding: 0 16px;
    margin-bottom: 20px;

    &_item {
      padding: 19px 0;
      display: flex;
      align-items: center;
      border-bottom: 1px solid $color-info-border;
      &:last-child {
        border: none;
      }
    }
  }
  &_block_normal {
    background: $color-info-bg;
    border-radius: 10px;
    padding: 16px;
    margin-bottom: 20px;
    &_item {
      padding-bottom: 20px;
      display: flex;
      align-items: center;
      &:last-child {
        padding-bottom: 0;
      }
    }
  }
  &_label {
    font-size: 16px;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.5);
    line-height: 22px;
  }
  &_label_light {
    font-size: 16px;
    font-weight: 600;
    color: #ffffff;
    line-height: 22px;
  }
  &_value {
    font-size: 20px;
    font-weight: 600;
    color: #ffffff;
    line-height: 28px;
    margin-left: auto;
  }
  &_value_mini {
    font-size: 16px;
    font-weight: 600;
    color: #ffffff;
    line-height: 22px;
    margin-left: auto;
  }
  &_tag {
    display: inline-block;
    font-size: 12px;
    font-weight: 400;
    color: rgba(255, 255, 255, 0.7);
    background-color: rgba(255, 255, 255, 0.1);
    line-height: 17px;
    padding: 2px 6px;
    margin-left: 8px;
  }
}

@media screen and (max-width: 768px) {
  .currency_link a {
    display: block;
  }
}
